import ConnectPlugin from "@/Components/connectPlugins";
import { GetAddonsList, GetToken } from "@/utilities/cookies";
import { notFound } from "next/navigation";
import createMetadata from "@/Components/Seo/createMetadata";

export async function generateMetadata() {
  const title = "Quotation details";

  return createMetadata({
    title: title,
  });
}

export default async function page() {
  const [token, addonList] = await Promise.all([GetToken(), GetAddonsList()]);

  if (addonList && addonList?.["product-quotation"]?.status == 0) {
    notFound();
  }

  if (!addonList?.["product-quotation"]) {
    notFound();
  }

  return (
    addonList &&
    addonList?.["product-quotation"]?.status == 1 &&
    ConnectPlugin.QuotationDetail && (
      <ConnectPlugin.QuotationDetail token={token} />
    )
  );
}
